import React, { Component } from 'react';
import ImageGallery from 'react-image-gallery';
import images from './helpers.js';
import logo from './images/relativity-formulae.svg';
import '../node_modules/react-image-gallery/styles/css/image-gallery.css';
import './App.css';
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';
const { ProgressBar, StatusBar } = require('@uppy/react');
const robodog = require('@uppy/robodog');

class App extends Component {
  constructor(props){
    super(props);
    this.state = {
      font: 'Ubuntu',
      fontSize: '12',
      message: 'Default statement'
    };
    this.uploadImage = this.uploadImage.bind(this);
    this.handleSelect = this.handleSelect.bind(this);
  }

  uploadImage(){
    const resultPromise = robodog.pick({
      params: {
        auth: { key: 'ee723b70d56f11e8b7809f4882ea3f24' },
      "steps": {
        ":original": {
          "robot": "/upload/handle"
        },
        "resized": {
          "use": [
            ":original"
          ],
          "robot": "/image/resize",
          "result": false,
          "height": 500,
          "imagemagick_stack": "v1.0.0",
          "resize_strategy": "fit",
          "width": 500,
          "zoom": false
        },
        "watermarked": {
          "use": [
            "resized"
          ],
          "robot": "/image/resize",
          "result": true,
          "imagemagick_stack": "v1.0.0",
          "text": [
            {
              "text": this.state.message,
              "size": 12,
              "font": this.state.font,
              "color": "#eeeeee",
              "valign": "bottom",
              "align": "right",
              "x_offset": 16,
              "y_offset": -10
            }
          ]
        }
      }
    }
    })
    resultPromise.then((bundle) =>{
      console.log(bundle.successful);
    })
  }
  
  handleSelect(e){
    e.preventDefault();
    let key = e.target.name;
    let value = e.target.value;
    this.setState({key: value}, ()=>console.log(this.state.key));
  }

  render() {
    return (
      <div className="App">
        <header className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
        </header>
        <h1>Choose Your Einstein</h1>
        <ImageGallery items={images} showThumbnails={false} showFullscreenButton={false} showPlayButton={false}/>
        <Form>
          Font
          <Form.Control name="font" as="select" onChange={(e)=> this.handleSelect(e)}>
            <option>Ubuntu</option>
            <option>Arial</option>
            <option>Comic Sans MS</option>
          </Form.Control>
          Font Size
          <Form.Control name="fontSize" as="select" onChange={(e)=> this.handleSelect(e)}>
            <option>Small</option>
            <option>Medium</option>
            <option>Large</option>
          </Form.Control>
          <Button onClick={(e)=> this.uploadImage(e.preventDefault())} variant="outline-primary" >Generate</Button>
        </Form>
        <div>Icons made by <a href="https://www.freepik.com/" title="Freepik">Freepik</a> from <a href="https://www.flaticon.com/" title="Flaticon">www.flaticon.com</a> is licensed by <a href="http://creativecommons.org/licenses/by/3.0/" title="Creative Commons BY 3.0">CC 3.0 BY</a></div>
      </div>
    );
  }
}

export default App;